 .app{
   display: grid;
   background-color: #dadbd3;
   place-items: center;
   height: 100vh;
 }
 .app__body{
   display:flex;
   background-color:#ededed;
   margin-top: 20px;
   height:95vh;
   width:85vw;
   box-shadow: -1px 4px 20px -6px rgba(0,0,0,0.75);
   min-width: 1000px;
   min-height: 95%;
 }
 .app-bg{
  position: absolute;
  background-color: #009688;
  width: 100%;
  display: grid;
  place-items: center;
  top: 0;
  z-index: 5;
  height: 15vh;
}